/* src/components/Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 1em 2em;
    color: white;
    position: relative;
}
span{
    color: red;
}

.navbar-brand {
    font-size: 1.2em;
    font-weight: bold;
    color: green;
}

.menu-btn {
    display: none;
    background: none;
    border: none;
    color: white;
    cursor: pointer;
}

.navbar-nav {
    list-style: none;
    display: flex;
    gap: 3em;
    margin: 0;
    padding: 0;
}

.navbar-nav li a {
    color: white;
    text-decoration: none;
    transition: color 0.5s;
    font-size: 16px;
}

.navbar-nav li a:hover {
    color: #c05050;
}

.btn-talk {
    background-color: #cad5e1;
    color: green;
    border: none;
    padding: 0.5em 1em;
    cursor: pointer;
    border-radius: 15px;
    transition: background-color 0.3s;
    font-size: 15px;
    list-style: none;
}
.btn-talk a {
    text-decoration: none;
    list-style: none;
}

.btn-talk:hover {
    background-color: white;
}

@media (max-width: 768px) {
    .menu-btn {
        display: block;
        font-size: 25px;
    }
    .menu-btn:hover{
        color: red;
        transition: color 0.5s;
    }
    .navbar-brand {
        font-size: 1.2em;
        font-weight: bold;
    }


    .navbar-nav {
        display: none;
        flex-direction: column;
        gap: 0;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background-color: #3e3e3e;
        padding: 1em 0;
    }

    .navbar-nav.open {
        display: flex;
    }

    .navbar-nav li {
        text-align: center;
        padding: 1em 0;
    }
    .navbar-nav li:hover{
        background-color: #333;
        border-radius: 20px;
    }
    .btn-talk {
        display: none;
    }
}
