/* src/components/Contact.css */
#contact {
    padding: 1em 2em;
    background-color: #f9f9f9;
    text-align: center;
}

#contact h2 {
    font-size: 3em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#063829, #7ac304);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

#contact p {
    font-size: 1.2em;
}

.contact-form {
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-group label {
    font-size: 1.1em;
    margin-bottom: 0.5em;
}

.form-group input,
.form-group textarea {
    padding: 0.75em;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1em;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: #007BFF;
}

.btn-submit {
    padding: 0.75em 2em;
    font-size: 1em;
    color: white;
    background-color: #007BFF;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.btn-submit:hover {
    background-color: #0056b3;
}

.success-message,
.error-message {
    margin-top: 1em;
    font-size: 1.2em;
    padding: 1em;
    border-radius: 4px;
}

.success-message {
    color: #155724;
    background-color: #d4edda;
    border: 1px solid #c3e6cb;
}

.error-message {
    color: #721c24;
    background-color: #f8d7da;
    border: 1px solid #f5c6cb;
}
