footer {
    background-color: #007bff;
    color: #fff;
    padding: 1em 2em;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.footer-section1,
.footer-section2,
.footer-section3 {
    flex: 1;
    min-width: 300px;
    text-align: center;
}

.footer-section1 img {
    width: 100px;
    height: auto;
}

.footer-card1 h1 {
    font-size: 24px;
    line-height: 1.5;
    margin: 10px 0;
    font-weight: bold;
    font-size: 2em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#69bb60, #d87777);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.footer-card1 h3{
    font-size: 24px;
    line-height: 1;
    margin: 10px 0;
    font-size: 1.3em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#b9c6b7, #550791);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


.footer-card2 a {
    color: #fff;
    font-size: 24px;
    margin: 0 5px;
}

.footer-card2 a:hover {
    color: #c05050;
}

.footer-section2 {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.footer-links,
.footer-services {
    margin: 10px;
    line-height: 30px;
}

.footer-links h1,
.footer-services h1 {
    font-size: 20px;
}

.footer-links ul,
.footer-services ul {
    list-style: none;
    padding: 0;
}

.footer-links a,
.footer-services a {
    color: #fff;
    text-decoration: none;
}

.footer-links a:hover,
.footer-services a:hover {
    color: #c05050;
}

.footer-contact h1,
.footer-contact h3,
.footer-contact h4,
.footer-contact a {
    margin: 5px 0;
    color: white;
}

.footer-contact h5, .footer-contact p {
    color: #fff;
}

.footer-contact a:hover {
    color: #c05050;
}

@media screen and (max-width: 768px) {
    footer {
        padding: 20px;
        flex-direction: column;
        align-items: center;
    }
    
    .footer-section1,
    .footer-section2,
    .footer-section3 {
        text-align: center;
        margin: 20px 0;
    }
    
    .footer-section2 {
        flex-direction: column;
        align-items: center;
    }
    
    .footer-links,
    .footer-services {
        width: 100%;
        text-align: center;
    }
    
    .footer-links ul,
    .footer-services ul {
        padding: 0;
    }
}

@media screen and (max-width: 480px) {
    .footer-section1 img {
        max-width: 100px;
    }
    
    .footer-card1 h1 {
        font-size: 18px;
    }
    
    .footer-card2 a {
        font-size: 20px;
    }
    
    .footer-links h1,
    .footer-services h1 {
        font-size: 18px;
    }
    
    .footer-links a,
    .footer-services a {
        font-size: 16px;
    }
}
