/* src/assets/styles/App.css */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    background: #333;
    padding: 1em;
    justify-content: space-around;
    display: flex;
}

nav ul {
    list-style: none;
    display: flex;
}

nav ul li a {
    color: white;
    text-decoration: none;
}






































section {
    padding: 2em;
    margin: 2em 0;
}

footer {
    text-align: center;
    padding: 1em;
    background: #333;
    color: white;
}
