/* src/components/About.css */
#about {
    padding: 1em  6em 1em 6em;
    background-color:white;
    text-align: center;
}

#about h1 {
    font-size: 3em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#578fe3, #b9be19);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

#about p {
    font-size: 1.2em;
    margin-bottom: 1em;
}

.about-us {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2em;
}

.home-image {
    flex: 0 0 30%;
    text-align: center;
}

.home-image img {
    max-width: 100%;
    height: auto;
    border-radius: 50%;
}

.about-me {
    flex: 0 0 70%;
    padding-left: 2em;
    text-align: left;
}

.about-me h1 {
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 0.5em;
}

.about-me p {
    font-size: 1.2em;
    margin-bottom: 1em;
}

.buttons {
    display: flex;
    gap: 2em;
}

.btn-hallo, .btn-portfolio {
    padding: 0.5em 1em;
    font-size: 1em;
    cursor: pointer;
    border: none;
    border-radius: 0.25em;
    transition: background-color 0.3s;
}

.btn-hallo {
    background-color: #007BFF;
    color: white;
}

.btn-hallo:hover {
    background-color: #0056b3;
}

.btn-portfolio {
    background-color: #6c757d;
    color: white;
}

.btn-portfolio:hover {
    background-color: #5a6268;
}

@media (max-width: 768px) {
    #about {
        padding: 1em 1em;
    }

    .about-us {
        flex-direction: column;
    }

    .home-image {
        margin-bottom: 1em;
    }

    .about-me {
        padding-left: 0;
    }

    .buttons {
        justify-content: center;
    }
}
