/* src/components/Home.css */
.home-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6em 6em 1em 6em;
    background-color: #f0f0f0;
}


.home-content {
    flex: 0 0 70%;
}

.home-image {
    flex: 0 0 30%;
    text-align: center;
}

.home-image img {
    max-width: 100%;
    height: auto;
    border-radius: 50%;
}

.logos {
    display: flex;
    gap: 1.5em;
    margin-bottom: 1em;
}
.icon:hover{
    color: black;
}

.icon {
    font-size: 2em;
    color: #5eabd8;
    cursor: pointer;
}

.greeting {
    font-weight: bold;
    font-size: 3em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#69bb60, #d87777);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.about-me{
    max-width: 600px;
}

.buttons {
    display: flex;
    gap: 2em;
}
.btn-portfolio ,
.btn-hallo {
    display: inline-block;
    padding: 0.5em 1em;
    font-size: 1em;
    cursor: pointer;
    border: none;
    border-radius: 0.25em;
    background-color: #6c757d;
    color: white;
    text-decoration: none; /* Remove underline from link */
    transition: background-color 0.3s;
}

.btn-portfolio:hover {
    background-color: #5a6268;
}
.btn-hallo:hover {
    background-color: #5a6268;
}


@media (max-width: 768px) {
    .buttons {
        justify-content: center;
    }
    .home-container {
        flex-direction: column-reverse;
        padding: 3em 1em;
    }

    .home-content {
        margin-bottom: 2em;
    }

    .home-image {
        flex: 1 0 auto;
        padding: 1.5em 2em;
    }
    .logos{
        justify-content: center;
        gap: 3em;
        padding-top: 1em;
    }
    .greeting{
        text-align: center;
    }
    .about-me{
        padding: 1em 2em;
    }
}
