/* src/components/Services.css */
#services {
    padding: 1em 2em;
    text-align: center;
}

#services h2 {
    font-size: 3em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#04eba5, #543ca1);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

#services p {
    font-size: 1.2em;
    margin-bottom: 2em;
}

.services-grid {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 2em;
}

.service-item {
    background-color: rgb(245, 245, 245);
    border: 2px solid #e5dede;
    border-radius: 8px;
    padding: 2em;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s;
}

.service-item:hover {
    transform: translateY(-10px);
}

.service-icon {
    font-size: 3em;
    color: #007BFF;
    margin-bottom: 1em;
}

.service-item h3 {
    font-size: 1.5em;
    margin-bottom: 0.5em;
}

.service-item p {
    font-size: 1em;
    color: #555;
    text-align: center;
}

@media (max-width: 768px) {

    .services-grid {
        width: 100%;
        flex-direction: column;
        align-items: center;
    }

    .service-item {
        width: 80%;
    }
}
