/* src/components/Portfolio.css */
#portfolio {
    padding: 1em 2em;
    background-color: #f0f0f0;
    text-align: center;
}
#portfolio h1 {
    font-size: 3em;
    margin-bottom: 0.5em;
    background: -webkit-linear-gradient(#43ad8d, #df5499);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

#portfolio p {
    font-size: 1.2em;
    margin-bottom: 2em;
}
.portfolio-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.portfolio-item {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    margin: 1em;
    padding: 1em;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between; /* Ensures even spacing */
    height: 400px; /* Set a fixed height for consistent layout */
}

.portfolio-item img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
}

.portfolio-item h2 {
    font-size: 1.5em;
    margin: 0.5em 0;
}

.portfolio-item p {
    flex: 1; /* Makes the paragraph take available space */
}

.project-link {
    background-color: #007BFF;
    color: white;
    padding: 0.5em 1em;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.3s;
}

.project-link:hover {
    background-color: #0056b3;
}
